.solution {
  padding: var(--spacing-3xl) 0;
  background-color: var(--color-white);
  position: relative;
  overflow: hidden;
}

.solution::before {
  content: '';
  position: absolute;
  top: -50%;
  right: -20%;
  width: 40%;
  height: 150%;
  background: radial-gradient(circle, rgba(102, 126, 234, 0.03) 0%, transparent 70%);
  pointer-events: none;
}

/* Use global .section-title styles from index.css */

.breakthrough-features {
  display: flex;
  flex-direction: column;
  gap: 0;
  margin-top: var(--spacing-lg);
  width: 100%;
}

.feature-item {
  padding: 0;
  background: transparent;
  transition: all 0.3s ease;
  width: 100%;
  position: relative;
  padding-bottom: var(--spacing-lg);
  margin-bottom: var(--spacing-base);
}

.feature-item:not(:last-child)::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 1px;
  background: var(--color-gray-200);
}

.feature-item:hover .feature-icon {
  transform: scale(1.1);
  box-shadow: 0 8px 16px rgba(102, 126, 234, 0.2);
}

.feature-content {
  flex: 1;
  text-align: left;
}

.feature-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-base);
  margin-bottom: var(--spacing-base);
  transition: color 0.3s ease;
}

.feature-icon {
  width: 48px;
  height: 48px;
  background: var(--color-primary);
  border-radius: var(--radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  font-size: 24px;
  flex-shrink: 0;
  transition: all var(--transition-base);
}

.feature-content h4 {
  font-size: var(--font-size-2xl);
  color: var(--color-gray-900);
  margin: 0;
  font-weight: var(--font-weight-semibold);
}

.feature-content > p {
  font-size: var(--font-size-lg);
  color: var(--color-gray-600);
  line-height: 1.6;
  margin-bottom: var(--spacing-base);
}

/* Expanded item with comparison */
.feature-item-expanded {
  padding-bottom: var(--spacing-base);
}

/* Comparison wrapper */
.comparison-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: var(--spacing-base);
  margin-top: var(--spacing-base);
  width: 100%;
}

.survey-comparison {
  background: var(--color-white);
  border-radius: var(--radius-lg);
  overflow: hidden;
  border: 1px solid var(--color-border);
  display: flex;
  flex-direction: column;
  height: 100%;
  transition: all var(--transition-base);
  box-shadow: var(--shadow-sm);
}

.survey-comparison:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.old-survey {
  border-color: var(--color-gray-300);
}

.new-survey {
  border-color: var(--color-primary);
  position: relative;
}

.survey-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--spacing-base);
  border-bottom: 1px solid var(--color-gray-100);
}

.survey-label {
  font-weight: var(--font-weight-semibold);
  font-size: var(--font-size-lg);
  color: var(--color-gray-900);
}

.new-survey .survey-label {
  color: var(--color-primary);
}

.survey-badge {
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--radius-full);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.old-badge {
  background: var(--color-gray-200);
  color: var(--color-gray-600);
}

.new-badge {
  background: var(--color-primary);
  color: var(--color-white);
}

.survey-content {
  flex: 1;
  padding: var(--spacing-base);
  font-size: var(--font-size-sm);
  min-height: 180px;
  overflow-y: auto;
  background: white;
}

.fixed-question {
  margin-bottom: var(--spacing-base);
  font-weight: var(--font-weight-medium);
  color: var(--color-gray-900);
}

.survey-options {
  margin-left: var(--spacing-lg);
  color: var(--color-gray-600);
  line-height: 2;
}

.chat-conversation {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

/* Advantages/Disadvantages section */
.survey-advantages,
.survey-disadvantages {
  padding: var(--spacing-sm) var(--spacing-base);
  border-top: 1px solid var(--color-gray-100);
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xs);
}

.survey-disadvantages {
  background: var(--color-gray-50);
}

.survey-advantages {
  background: rgba(102, 126, 234, 0.05);
}

.advantage-item,
.disadvantage-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  font-size: var(--font-size-sm);
}

.advantage-item i {
  color: var(--color-primary);
  font-size: var(--font-size-base);
}

.disadvantage-item i {
  color: var(--color-gray-500);
  font-size: var(--font-size-base);
}

.advantage-item span {
  color: var(--color-gray-700);
  font-weight: var(--font-weight-medium);
}

.disadvantage-item span {
  color: var(--color-gray-600);
}


/* Chat bubbles */
.chat-bubble {
  background: var(--color-primary);
  color: var(--color-white);
  padding: var(--spacing-sm) var(--spacing-base);
  border-radius: 18px;
  margin-bottom: var(--spacing-sm);
  max-width: 85%;
  font-size: var(--font-size-sm);
  line-height: var(--line-height-normal);
}

.response-bubble {
  background: var(--color-gray-100);
  color: var(--color-gray-800);
  padding: var(--spacing-sm) var(--spacing-base);
  border-radius: 18px;
  margin-bottom: var(--spacing-sm);
  max-width: 85%;
  margin-left: auto;
  text-align: left;
  font-size: var(--font-size-sm);
  line-height: 1.5;
}

/* Typing indicator */
.chat-bubble.typing {
  display: flex;
  align-items: center;
  padding: var(--spacing-sm) var(--spacing-lg);
  min-width: 60px;
  max-width: 80px;
}

.chat-bubble.typing::after {
  content: '...';
  display: inline-block;
  animation: typing 1.4s infinite;
  font-size: var(--font-size-lg);
  letter-spacing: 0.2em;
  margin-left: -0.2em;
}

@keyframes typing {
  0%, 60%, 100% {
    opacity: 0.2;
  }
  30% {
    opacity: 1;
  }
}

/* Control and Treatment Group Styles */
.control-group {
  border: 1px solid var(--color-gray-300);
}

.treatment-group {
  border: 1px solid var(--color-blue);
}

.control-badge {
  background: var(--color-gray-200);
  color: var(--color-gray-600);
}

.treatment-badge {
  background: var(--color-blue);
  color: white;
}

.group-description {
  min-height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: var(--spacing-sm) 0;
}

.protocol-desc {
  font-size: var(--font-size-sm);
  color: var(--color-gray-600);
  margin-bottom: var(--spacing-base);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.question-example {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: var(--spacing-base);
}

.question-example p {
  margin: 0;
  text-align: center;
  font-size: var(--font-size-sm);
  color: var(--color-gray-800);
  font-style: italic;
}

.question-example .sub-text {
  margin-top: var(--spacing-xs);
  font-size: var(--font-size-xs);
  color: var(--color-gray-500);
  font-style: normal;
}

.effect-highlight {
  display: flex;
  align-items: center;
  gap: var(--spacing-sm);
  background: rgba(102, 126, 234, 0.1);
  padding: var(--spacing-sm) var(--spacing-base);
  border-radius: var(--radius-sm);
  margin-top: var(--spacing-base);
}

.effect-highlight i {
  color: var(--color-blue);
}

.effect-highlight span {
  color: var(--color-blue);
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-sm);
}

.example-result {
  padding: var(--spacing-base) 0;
  text-align: center;
  border-top: 1px solid var(--color-gray-100);
  background: transparent;
}

.example-result span {
  font-size: var(--font-size-sm);
  color: var(--color-gray-700);
  font-weight: var(--font-weight-medium);
  background: transparent;
}

.bias-reveal {
  text-align: center;
  margin-top: var(--spacing-lg);
  padding: var(--spacing-sm) 0;
}

.bias-reveal span {
  color: var(--color-gray-700);
  font-size: var(--font-size-sm);
  font-style: italic;
}

.method-insight {
  margin-top: var(--spacing-lg);
  padding: var(--spacing-base);
  background: rgba(102, 126, 234, 0.05);
  border-left: 3px solid var(--color-blue);
  border-radius: var(--radius-sm);
}

.method-insight p {
  font-size: var(--font-size-sm);
  color: var(--color-gray-700);
  line-height: 1.6;
  margin: 0;
}

/* Meng equation styles */
.meng-equation-container {
  margin: var(--spacing-lg) 0;
  padding: var(--spacing-lg);
  background: var(--color-gray-50);
  border-radius: var(--radius-lg);
  border: 1px solid var(--color-gray-200);
}

.meng-equation {
  width: 100%;
  height: auto;
  max-width: 800px;
  margin: 0 auto;
  display: block;
  color: var(--color-gray-900);
}

.equation-explanation {
  margin-top: var(--spacing-lg);
  text-align: center;
}

.equation-explanation p {
  font-size: var(--font-size-sm);
  color: var(--color-gray-700);
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto;
}


@media (max-width: 740px) {
  .breakthrough-features {
    gap: var(--spacing-lg);
  }
  
  .feature-item {
    padding: var(--spacing-xl);
  }
  
  .feature-content {
    text-align: left;
  }
  
  .feature-content h4 {
    font-size: var(--font-size-xl);
  }
  
  .feature-content p {
    font-size: var(--font-size-base);
  }
  
  .comparison-wrapper {
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
  }
  
  .survey-comparison {
    padding: var(--spacing-base);
  }
  
  .chat-bubble,
  .response-bubble {
    max-width: 90%;
    font-size: var(--font-size-sm);
  }
  
  .cost-comparison {
    grid-template-columns: 1fr;
    gap: var(--spacing-lg);
  }
}